.home {

    .homeContent {
        background: linear-gradient(115deg, $color-1 50%, transparent 100%);
        min-height: $minHeight;
        width: 100%;
        min-height: 500px;
        border-radius: $borderRadius;
        position: relative;
        overflow: hidden;

        &:after {
            content: '';
            position: absolute;
            height: 100%;
            width: inherit;
            background: url('../../media/image-home.jpg') no-repeat center/cover;
            opacity: 30%;
            z-index: -1;
            filter: brightness(20%);
        }

        .content {
            position: absolute;
            top: 20%;
            left: 13%;
            font-family: $font-1;

            h1 {
                font-size: 5rem;
                font-family: $font-2;
                color: $white;
                text-transform: uppercase;
                text-shadow: 0.5px 2px 0.5px $black;
            }

            h2 {
                font-size: 2.5rem;
                padding: 1.5rem 0 7rem;
                color: $white;
                text-shadow: 0.5px 2px 0.5px $black;
            }

            .pdf {
                a {
                    font-size: 1.5rem;
                    background: $color-3;
                    color: $black;
                    padding: 1rem 2rem;
                    border-radius: $borderRadius;
                    box-shadow: 0 4px 1px rgba(15, 15, 15, 0.795);
                    transition: 0.2s ease;

                    &:hover {
                        background: $color-4;
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 850px) {
    .homeContent {
        margin: 0 !important;
    }
}

@media screen and (max-width:580px) {
    .homeContent {
        &:after {
            background: url('../../media/image-home.jpg') no-repeat 60% 50%/cover !important;
        }

        h1 {
            font-size: 3rem !important;
        }

        h2 {
            font-size: 1.8rem !important;
        }
    }
}